.btnDis{
    font-family: "Raleway", Sans-serif;
    font-size: 18px;
    font-weight: 600;
    letter-spacing: 0.5px;
    fill: #020710;
    color: #020710;
    background-color: transparent;
    background-image: linear-gradient(90deg, #E75469 0%, #A93EF8 100%);
    border-radius: 10px 10px 10px 10px;
    box-shadow: 0px 4px 20px 0px rgba(169.00000000000017, 62, 248, 0.23921568627450981);
    padding: 15px 45px 15px 45px;
}
.btnDis:hover{
    color: #fff;
}
.topSide{
    /* background-image:url("../img/topMainBG.png"); */
    background-size:cover;
    background-repeat:no-repeat;
    position: relative;
    height: 600px;
}

.imgBG{
    position:absolute;
    width: 550px;
    height: auto;
    /* top: 200px; */
}
.imgBG{
    filter: drop-shadow(5px 5px 5px #757575);
    transform: translatey(0px);
    animation: float 6s ease-in-out infinite;
}
@keyframes float {
    0% {
    -webkit-filter: drop-shadow(5px 5px 5px #757575);
    filter: drop-shadow(5px 5px 5px #757575);;
        transform: translatey(0px);
    }
    50% {
    -webkit-filter: drop-shadow(5px 5px 5px #757575);
    filter: drop-shadow(5px 5px 5px #757575);
        transform: translatey(-50px);
    }
    100% {
    -webkit-filter: drop-shadow(5px 5px 5px #757575);
    filter: drop-shadow(5px 5px 5px #757575);
        transform: translatey(0px);
    }
}
